.news {
  padding: 30px 0 50px;

  .button--transparent {
    border-color: $color-black;
    color: $color-black;
  }

  .container {
    max-width: 1024px;
  }

  .news__list {
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin: 0;
    padding: 0;

    li {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background-color: $color-light-gray;
        bottom: -25px;
      }

      &:last-of-type {
        &::after {
          content: none;
        }
      }

      h3 {
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        text-align: left;
      }

      p {
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        text-align: left;
      }

      div {
        display: flex;
        justify-content: space-between;

        span {
          font-size: 15px;
          font-weight: 300;
          line-height: 22.5px;
          text-align: center;
          color: $color-gray;
        }

        a {
          font-size: 15px;
          font-weight: 300;
          line-height: 22.5px;
          text-align: center;
          color: $color-gray;

          &:hover,
          &:focus,
          &:active {
            opacity: 50%;
          }
        }
      }
    }
  }
}

.news__container {
  display: grid;
  grid-template-columns: 1fr;
  margin: 50px 0;
  gap: 30px;

  @include vp-1024 {
    grid-template-columns: 3fr 2fr;
  }

  img {
    object-fit: cover;
    object-position: center 60%;
    width: 100%;
    height: 100%;
    filter: grayscale(1);
    display: none;

    @include vp-1024 {
      height: 100%;
      display: block;
    }
  }
}

.news--list {
  margin: 30px 0;

  .container {
    max-width: unset;
  }

  .news__list {
    display: grid;
    grid-template-columns: 1fr;


    @include vp-1024 {
      grid-template-columns: 1fr 1fr;
    }

  li {
      &:nth-last-of-type(2) {
        &::after {
          content: none;
        }
      }
    }
  }
}
